import { 
    React,
    useState,
    useEffect 
  } from 'react';
  import ReactDOM from "react-dom";
  import { useForm } from "react-hook-form";
  import {
    Link,
    useParams
  } from 'react-router-dom';

function CivilizationApi(){
    
    const {id}=useParams();// es lo mismo que esto const id=useParams().id;
    console.log(id)

    const [civilization,setCivilization] = useState([])

    useEffect( ()=>{
        const obtenerDatos = async () => {
            const data = await fetch(`https://age-of-empires-2-api.herokuapp.com/api/v1/civilization/${id}`);
            const users = await data.json();
            setCivilization(users);
        }
        obtenerDatos();
    },[id] )

    

    return (
        <div>
            <h2>{civilization.name}</h2>
            <p>Expansion: {civilization.expansion}</p>
            <p>Team bonus: {civilization.team_bonus}</p>
            <p>Civilization bonus: {civilization.civilization_bonus}</p>
            <a href="https://www.youtube.com/watch?v=Q9YClZMj9-M">https://www.youtube.com/watch?v=Q9YClZMj9-M</a>
        </div>


    )
}



  export default CivilizationApi;