import {React,
    useState,
    useEffect
} from 'react';
import { BrowserRouter as Router,
    Switch,
    Route,
    Link,
    NavLink
} from 'react-router-dom';
import ReactDOM from "react-dom";
import ExampleClicks from './components/clicks';
import ExampleApi from './components/consumoapi'
import ExampleHome from './components/home'
import ExampleApi2 from './components/consumoapi2'
import CivilizationApi from './components/civilization'
import Game from './components/tateti/tateti'

function Example() {
     
    

    return (
        <Router>
        <div>
            <Link to="./" class="btn btn-dark" activeClassName="active">
                Inicio
            </Link>
            <Link to="/game" class="btn btn-dark" activeClassName="active">
                tateti
            </Link>
            <Link to="/clicks" class="btn btn-dark" activeClassName="active">
                clicks
            </Link>
            <Link to="/api" class="btn btn-dark" activeClassName="active">
                api
            </Link>
            <Link to="/api2" class="btn btn-dark" activeClassName="active">
                api2
            </Link>
            
            <hr/>

            <Switch>
                <Route path="/api2/:id" exact>
                    <CivilizationApi/>
                </Route>
                <Route path="/" exact>
                    <ExampleHome/>
                </Route>
                <Route path="/clicks" exact>
                    <ExampleClicks/>
                </Route>
                <Route path="/api" exact>
                    <ExampleApi/>
                </Route>
                <Route path="/api2" exact>
                    <ExampleApi2/>
                </Route>
                <Route path="/game" exact>
                    <Game/>
                </Route>
            </Switch>
            <hr/>
        </div>
        </Router>
    )
}


ReactDOM.render(
    <Example />,
    document.getElementById('root')
  );